body {
  margin: 0;
  font-family: "Lato", sans-serif;
  
}

.size{
  
    display: block;
    align-content: center;
    width: 975.2px;
    height: 800px;
}

.bg{
  background-attachment: fixed;
  /* background-size:100%; */
  height: 1500px;
  font-family: "Lato", sans-serif;
  background-color: rgb(23, 13, 23);
  background-image: url('./components/images/community-bg.png');

}
.text-center{
  color: rgb(255, 254, 253);
}
.sidebar {
  margin: 0;
  padding: 0;
  width: 200px;
  background-color: #221c24;
  position: fixed;
  height: 100%;
  overflow: auto;
}

.sidebar a {
  display: block;
  color: black;
  padding: 16px;
  text-decoration: none;
}
 
/* .sidebar a.active {
  background-color: #04AA6D;
  color: white;
} */

.sidebar a:hover:not(.active) {
  background-color: #555;
  color: white;
}

div.content {
  margin-left: 200px;
  padding: 1px 16px;
  height: 1000px;
}

@media screen and (max-width: 700px) {
  .sidebar {
    width: 100%;
    height: auto;
    position: relative;
  }
  .sidebar a {float: left;}
  div.content {margin-left: 0;}
}

@media screen and (max-width: 400px) {
  .sidebar a {
    text-align: center;
    float: none;
  }
}

